import modelExtend from 'dva-model-extend'
import { query } from '../../services/banner'
import { pageModel } from '../../models/common'
import queryString from 'query-string'

interface Istate {
  currentItem: any,
  modalVisible: boolean,
  selectedRowKeys: any[]
}

export default modelExtend(pageModel, {
  namespace: 'waitingsong',
  state: {
    selectedRowKeys: [],
    modalVisible: false,
    // modalType: 'create',
    currentItem: {},
  },
  subscriptions: {
    setup ({ dispatch, history }: any) {
      history.listen((location: any) => {
        if (location.pathname === '/song/waitingsong') {
          dispatch({
            type: 'query',
            payload: {
              status: 2,
              ...queryString.parse(location.search),
            },
          })
        }
      })
    },
  },
  effects: {
    * query ({ payload }: any, { call, put }: any) {
      const data = yield call(query, payload)
      if (data.success) {
        yield put({
          type: 'querySuccess',
          payload: {
            list: data.data,
            pagination: {
              current: Number(payload.page) || 1,
              pageSize: Number(payload.pageSize) || 10,
              total: data.total,
            },
          },
        })
      } else {
        throw data
      }
    },
  },
  reducers: {
    updateState (state: Istate, { payload }: any) {
      return {
        ...state,
        ...payload,
      }
    },
  },
})



