import { getLocalStorage } from '../utils/utils'
import config from '../utils/config'
import request from '../utils/request'

const { api } = config
const { indexbanner, focusList, modifyFocus } = api

export function query (params: any) {
  return request({
    url: indexbanner,
    method: 'get',
    data: params,
  })
}
export function queryFocusList (params: any) {
  return request({
    url: focusList,
    method: 'get',
    data: params,
  })
}
export function modifyFocusSer (params: any, method: any) {
  console.log('method', method)
  const SystemAdmin = getLocalStorage('SystemAdmin')
  const url = modifyFocus.replace(':uid', SystemAdmin.id)
  return request({
    url: method.method === 'put' ? url.replace(':id', params.id) : url.replace('/:id', ''),
    ...method,
    data: params,
  })
}



